import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer
} from "recharts";

const REWARD_KEY = "smogbusters_rewards";

export default function Rewards() {
  const navigate = useNavigate();
  const [rewards, setRewards] = useState([]);

  useEffect(() => {
    const saved = JSON.parse(localStorage.getItem(REWARD_KEY)) || [];
    setRewards(saved);
  }, []);

  const total = rewards.reduce((sum, r) => sum + r.amount, 0);

  return (
    <div className="tasks-page">
      <div className="tasks-overlay">

        <button className="back-home-btn" onClick={() => navigate("/")}>
           Back to Home
        </button>

        <h2 className="tasks-title">Rewards</h2>
        <p className="tasks-subtitle">
          Your eco impact is paying off 
        </p>

        <h3 style={{ textAlign: "center" }}>
          Total Earned: <strong>${total} USD</strong>
        </h3>

        <div style={{ width: "100%", height: 300, marginTop: 30 }}>
          <ResponsiveContainer>
            <LineChart data={rewards}>
              <XAxis dataKey="date" />
              <YAxis />
              <Tooltip />
              <Line
                type="monotone"
                dataKey="amount"
                strokeWidth={3}
              />
            </LineChart>
          </ResponsiveContainer>
        </div>

      </div>
    </div>
  );
}
